<template>
    <div>
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;margin-top:15px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.name" placeholder="编号"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getUsers">查询</el-button>
                </el-form-item>
                <!-- <el-button type="primary" @click="exportWord">导出<i class="el-icon-upload el-icon--right"></i></el-button> -->
            </el-form>
        </el-col>


        <el-table :data="tableData()" style="width: 100%" >
            <el-table-column prop="bicycleNo" label="编号" width="250">
            </el-table-column>
            <el-table-column prop="color" label="颜色" width="250">
            </el-table-column>
            <el-table-column prop="statusName" label="是否租赁" width="250">
            </el-table-column>
            <!--第二步  开始进行修改和查询操作-->
            <el-table-column label="操作" align="center" min-width="100">

                <template slot-scope="scope">
                    <el-button type="danger" @click="changeview(scope.row)">归还</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-dialog
            title="提示"
            :visible.sync="dialogVisible"
            width="30%">
            <span>你需要支付{{money}}</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="deleteUser()">确 定</el-button>
            </span>
            </el-dialog>
    </div>

</template>

<script>
    import axios from 'axios';
    import qs from 'qs';
    export default {
        name: 'home',
        inject:['reload'],
        data() {
            return {
                userInfoList: [],
                
                filters: {
                    name: ''
                },
                token:'',
                user:{},
                dialogVisible: false,
                money:'',
                bicycle:{}
            }
        },

        methods: {
            changeview(row){
                console.log(row);
                this.bicycle=row;
                this.axios.get("bicycle/money/"+row.bicycleNo).then(res=>{
                    console.log("res: ",res);
                    if(res.data.code===200){
                        this.money = res.data.data;
                    }
                })
                this.dialogVisible = true;
            },
            //  exportWord() {
            //     this.$exportExcel({
            //         url: "/user/exportExcel",
            //         data: {},
            //         responseType: "blob",
            //         fileName: "用户记录",
            //         method: 'post'
            //     });
            // },
            loadData() {
                // let param = {filter:this.filters.name};
                this.axios.get(this.$baseUrl()+'/bicycle/rentover?userId='+this.user.id).then((res) => {
                    console.log(res);                    
                    this.userInfoList = res.data.data;
                });

            },
             tableData() {
                let status = {
                    0: "未租用",
                    1: "已租用"
                };
                return this.userInfoList.map(it => {
                    it.statusName = status[it.isRent];
                    return it;
                });
            },
            getUsers() {
                this.axios.get("bicycle/findByNoLikeRent",{
                    params:{
                        bicycleNo:this.filters.name
                    }
                }).then(res=>{
                    console.log("res:",res);
                    this.userInfoList = res.data.data;
                })
            },
            addUser(){

            },
            deleteUser() {
                this.axios.post("bicycle/returnCar",this.$qs.stringify({id:this.bicycle.id,money:this.money})).then(res=>{
                    console.log("res: ",res);
                    if(res.data.code===200){
                        this.$message({
                            message:"退还成功",
                            type:'success'
                        })
                        this.reload();
                    }else{
                            this.$message({
                                message: res.data.message,
                                type:'error'
                            });
                            
                        this.reload();
                    }
                })
            },
           
        },
        created(){
            this.user = this.$store.state.user.accessToken;
            console.log("user: ",this.user);
            
            this.loadData();
        }
    }
</script>

<style>
body {
        background: #DFE9FB;
    }
.demo-ruleForm{
  margin-left: 10%;  
  width: 80%;
}
.el-table .success-row {
    background: #f0f9eb;
  }
</style>


